import React from 'react'
import '../../../style/App.css';

let ListFriend = React.memo(props => {
    debugger
    let DialogItem = props.friend.map((i) => { return (
        // onDoubleClick={() => {props.itsRename('PROFILE', {list:[i._id], id:false});}}
       <div className={"List-item-Party-Friend"}>
           <img id={"ava-min"} src={i.Description.PhotoMin.origin || i.Description.PhotoMin.default} alt={"Pic"}/>
           <div id={'text-description-friend-party'}>
                <div>{i.Description.Nickname.map(e=>e).join(' ')}</div>
                <div>{i.Description.Status}</div>
           </div>
           {props.setting && <input type={'checkbox'} onClick={()=>{props.itsRename("SELECTED-FRIEND", i._id)}}/>}
       </div>
    )});
    return <div>
        {DialogItem}
    </div>
});

export default ListFriend;



