import React, {useEffect, useState} from 'react';
import './style/style.css';
import {connect} from "react-redux";

import {withRouter} from "react-router";
import {withAuthRedirect} from "../../hoc/withAuthRedirect";
import {compose} from "redux";
import ListFriend from "./listFriend/ListFriend";
import LoAd from "../../conponent/load";
import {deleteParty,GetListFriend,GetListMessages,GetListParty,SendMessage,SendSetting} from "./MessageReducer";
import {getFriend, getMessage, getParty} from "../../user-selector/MessageSelector";
import FormToSend from "./ComponentMessage/FormToSend";
import ListParty from "./listFriend/ListParty";
import ListMessage from "./listMessages/ListMessage";

const MessageCont= props => {

    let [text, setText] = useState('');
    let [img, setImg] = useState('');
    let [nameParty, setNameParty]= useState('');

    let [selectedFriend, setSelectedFriend]=useState([props.state.user]);
    let [selectedParty, setSelectedParty]=useState([]);

    let [message,setMessage]=useState([]);
    let [friend,setFriend]=useState([]);
    let [party,setParty]=useState([]);

    let [user, setUser]=useState(props.state.user);
    let [profile, setProfile] = useState({id:false,list:[]});

    let [setting, setSetting]=useState(false);
    let [list, setList]= useState(false);

    useEffect(() => {
        list?props.GetListFriend():props.GetListParty();
    }, [list,setting]);

    useEffect(() => {
        props.GetListMessages(profile);
    }, [profile]);
    useEffect(() => {
        setText(text);
    }, [text]);

    useEffect(() => {
        setMessage(props.message);
        setFriend(props.friend)
        setParty(props.party)
    }, [props.message, props.friend, props.party]);

    let itsRename=(name,value)=> {
        switch (name) {
            case "TEXT" :return  setText(value);
            case "IMG" :return setImg(value);
            case "PROFILE" :
                return setProfile(value);
            case "NAME-PARTY" :return setNameParty(value);
            case "SEND" :
                return props.SendMessage({
                    'idParty':profile.id
                    ,'dialog':profile.list
                    ,'user':user
                    ,'text':text
                    ,'img':img
                    ,'date':new Date().toTimeString().replace(/ .*/, '')
            });
            case "SETTING" :return setSetting(value);
            case "SELECTED-FRIEND" :
                let f=selectedFriend ,fa=f.filter(r=>r===value).length;
                if (fa!==0){
                    let faq=f.indexOf(value);
                    f.splice(faq,faq+1)
                }else{
                    f.push(value)
                }
                return setSelectedFriend(f);
            case "SELECTED-FRIEND-ADD-PARTY" :

                selectedFriend.length>1 && props.SendSetting({command:'FRI-ADD-PARTY',list:selectedFriend, nameParty:nameParty});
                return setSetting(true);
            case "SELECTED-PARTY" :
                let b=selectedParty, be=b.filter(r=>r===value).length;
                if (be!==0){
                    let bea=b.indexOf(value);
                    b.splice(bea,bea+1)
                }else{
                    b.push(value)
                }
                return setSelectedParty(b);
            case "SELECTED-PARTY-DEL" :
                setSetting(true);
                props.SendSetting({command:'PAR-DEL',list:selectedParty});
                return setProfile({id:false, list:[]});
            case "LEAVING-THE-PARTY" :
                props.SendSetting({command:'LEAVING-THE-PARTY',list:value});
                return setSetting(true);
            default :
                return 'null method'
        }
    };

    let goInput=(action)=>{itsRename(action.target.name, action.target.value)};
    return (
        <div className={'mess_Section'}>
        {/*{*/}
        {/*    message.length===0 &&*/}
        {/*    friend.length===0 &&*/}
        {/*    party.length===0 &&*/}
        {/*    <div><LoAd/></div>*/}
        {/*}*/}
            <div className={'mess-List_Mess'}>
                 <ListMessage message={message} user={user} profile={profile} />

                 <div className={'mess_List_Form'}>
                     {profile.id && <FormToSend itsRename={itsRename} text={text} />}
                 </div>

            </div>

            <div className={'mess_List_Dia'}>
                <div className={'mess_List_Dia_Panel'}>
                    <div onClick={()=>{setList(!list)}} id={'Button-Message'}>
                        {list?'Friend':'Party'}
                    </div>

                    <div>
                        {list
                            ?<div>
                                {setting
                                    ?<div className={'Button-Flex'}>

                                        <div onClick={()=>{itsRename("SELECTED-FRIEND-ADD-PARTY")}}
                                             id={'Button-Message'}>
                                            {'Done'}
                                        </div>

                                        <div onClick={()=>{setSetting(!setting)}}
                                             id={'Button-Message'}>
                                            {'Cancel'}
                                        </div>
                                    </div>

                                    :<div onClick={()=>{setSetting(true)}}
                                          id={'Button-Message'} className={'Create-Place'}>
                                        {'Create Party'}
                                    </div>
                                }
                                <div>
                                    {setting && <input onChange={goInput}
                                                       name={'NAME-PARTY'}
                                                       value={nameParty}
                                                       id={'input-name'}
                                                       placeholder="Entry nickname Party"/>
                                    }
                                </div>
                            </div>
                            :<span>
                               { setting
                                   ?<div className={'Button-Flex'}>
                                       <div onClick={()=>{itsRename("SELECTED-PARTY-DEL")}}
                                            id={'Button-Message'}>
                                           {'Done'}
                                       </div>
                                       <div onClick={()=>{setSetting(!setting)}}
                                            id={'Button-Message'}>
                                           {'Cancel'}
                                       </div>
                                   </div>
                                   :<span>
                                          <div onClick={()=>{setSetting(true)}} id={'Button-Message'}>
                                             {setting?'Done':'Deleted Party'}
                                          </div>
                                    </span>
                               }
                         </span>
                        }
                    </div>
                </div>

                 <div className={'mess_List_Dia_List'}>
                       {list &&  <ListFriend friend={friend}
                                             itsRename={itsRename}
                                             selectedFriend={selectedFriend}
                                             setting={setting}/>
                       }
                       {!list &&  <ListParty party={party}
                                             user={user}
                                             itsRename={itsRename}
                                             selectedParty={selectedParty}
                                             setting={setting}/>
                       }
                 </div>
            </div>
    </div>)

};
let mapStateToProps = (state) => {
    return {
        state: state.LoginReducer.user
        , message: getMessage(state)
        , friend: getFriend(state)
        , party: getParty(state)
    }
};
export default compose(
    connect(mapStateToProps, {GetListFriend, GetListMessages, SendMessage, SendSetting ,GetListParty,deleteParty})
    , withRouter
    , withAuthRedirect
)(MessageCont)