import React from 'react'

let ListMessage = React.memo(props => {

    let messageItem =props.profile.id && props.message!==0 && props.message.ListMessage.map(i => {
            return (
                <div>{props.user === i.user
                    ? <div className={"MESSAGE_ITEM"} id={'USER'}>
                        <div>
                            {i.text}
                            {i.img !== '' && <img alt={'Pic Send for you'} src={i.img}/>}
                        </div>
                        <div>{i.date}</div>
                    </div>
                    : <div className={"MESSAGE_ITEM"} id={'PROFILE'}>
                        <div>{i.text}</div>
                        <div>{i.date}</div>
                    </div>
                }
                </div>
            );
        }
    );

    return (
        <div>
            <div className={'mess_List_Desc'}>
                {!props.profile.id &&
                    <div>
                        <img id={"mess_img"}
                             src={'https://avatars.mds.yandex.net/get-pdb/34158/6e006c76-9b84-4ad1-8d07-b31e7e8d284b/s600'}
                             alt={"Pic"}/>
                    </div>
                }

                {props.profile.id &&
                <div>
                    <div>
                        <div>
                            <img id={"ava-min"}
                                 src={props.message.AvaParty}
                                 alt={"Pic"}/>
                        </div>
                        <div>Name Party
                            {props.message.NameParty.map(e => e).join(' ')}
                            , numb Person {props.message.NumberOfPerson}
                            , numb message {props.message.NumberMessage}
                        </div>
                    </div>
                    <div className={'mess_List_List'}>
                        {
                            props.profile.id
                            && <div>{messageItem}</div>
                        }
                    </div>
                </div>
                }

            </div>
        </div>
    )
})
export default ListMessage;