import React, {useEffect} from 'react'
import send from '../../../style/img/send.png'

let FormToSend = props => {



    let io=(action)=>{
        props.itsRename('TEXT',action.target.value)
    }
    let se=()=>{
        props.itsRename('SEND')
    }

    useEffect(
        ()=>document.addEventListener('keypress', e=>{
        e.ctrlKey && e.code==='Enter' && props.itsRename('SEND')
    }),[]);


    return (<form onChange={io}>
                   <textarea className={'text-area'} >{props.text}</textarea>
                   <img src={send}  className='send-mess' onClick={se} value={'send'} />
            </form>
    )

}

export default FormToSend;



